$color-primary: #55c57a;
$color-primary-light: #7ed56f;
$color-primary-dark: #28b485;

$color-body-demo: #777;

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: $color-body-demo;
  padding: 3rem;

  box-sizing: border-box;
}

.nav {
  display: flex;
  flex-direction: column;
}

.nav-list {
  color: $color-primary;
  font-size: 1rem;
  font-weight: 500;
}
